import { ProductTypeCode } from '../enums/ProductTypeCode';

/* 
   Appliance registration info
   This model is used when a user registers new appliance in the system 
*/
export class ApplianceRegistration {
 
    // Appliance name (optional) 
    public name: string;
 
    // Account Id 
    public accountId: string;
 
    // FolderId 
    public folderId: string;
 
    // Physical machine identifier (serial number) 
    public machineId: string;
 
    // Product type code (Edge Model) 
    public productType: ProductTypeCode;
 
    // Appliance SW configuration id 
    public configurationId: string;
 
    // Appliance SW configuration version id 
    public configurationVersionId: string;
 
    // Custom profile to override general package parameters 
    public deviceProfileId: string;
 
    constructor(name?: string, accountId?: string, folderId?: string, machineId?: string, productType?: ProductTypeCode, configurationId?: string, configurationVersionId?: string, deviceProfileId?: string) { 
        this.name = name;
        this.accountId = accountId;
        this.folderId = folderId;
        this.machineId = machineId;
        this.productType = productType;
        this.configurationId = configurationId;
        this.configurationVersionId = configurationVersionId;
        this.deviceProfileId = deviceProfileId;
    }

}


