import { ApplianceStatusCode } from '../enums/ApplianceStatusCode';
import { ApplianceCapabilities } from '../entities/ApplianceCapabilities';
import { Thresholds } from '../common/Thresholds';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Appliance (referred also as Edge or Device) is the edge device in the camera network hosting the analytics agent(s) 
*/
export class Appliance extends BaseEntity {
 
    // Appliance name 
    public name: string;
 
    // Account Id 
    public accountId: string;
 
    // Folder Id 
    public folderId: string;
 
    // Account status: UNDEFINED | ACTIVE | SUSPENDED 
    public status: ApplianceStatusCode;
 
    // When was the last status report update [Epoch milliseconds Timestamp] 
    public statusReportedOn: number;
 
    // Appliance pairing ke (used to pair the device to the account on first installation) 
    public pairingKey: string;
 
    // When appliance was deleted [Epoch milliseconds Timestamp] 
    public deletedOn: number;
 
    // When appliance last reported status [Epoch milliseconds Timestamp] 
    public reportedOn: number;
 
    // Physical machine identifier (serial number) 
    public machineId: string;
 
    // Appliance SW configuration id 
    public configurationId: string;
 
    // Appliance SW configuration version id 
    public configurationVersionId: string;
 
    // Appliance capabilities 
    public capabilities: ApplianceCapabilities;
 
    // Integration target id for local events integration 
    public eventIntegrationId: string;
 
    // Integration target id for local metadata integration 
    public metadataIntegrationId: string;
 
    // Custom profile to override general package parameters 
    public deviceProfileId: string;
 
    // Device path in the account hierarchy 
    public path: string;
 
    // Health notifications thresholds 
    public healthThresholds: Thresholds;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


