/* 
   Video stream type (source) code 
*/
export enum StreamTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // RTSP stream [1] 
    RTSP = 1,
 
    // ONVIF stream [2] 
    ONVIF = 2,
 
    // Video clip from file [3] 
    CLIP = 3,
 
    // RTP stream [4] 
    RTP = 4,
 
    // MULTICAST stream [5] 
    MULTICAST = 5,
 
    // Download clip from http(s) [6] 
    HTTP = 6,
 
}