import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/*  
*/
export class SearchEventFindRequest {
 
    // Session Id 
    public id: string;
 
    // Filter by sensor Id(s) (empty for no filter) - multiple values enabled 
    public sensorId: string[];
 
    // Filter by object type(s) (empty for no filter) - multiple values enabled 
    public objectType: ObjectTypeCode[];
 
    // Filter search events after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter search events before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(id?: string, sensorId?: string[], objectType?: ObjectTypeCode[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) { 
        this.id = id;
        this.sensorId = sensorId;
        this.objectType = objectType;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


