import { ApplianceStatusCode } from '../enums/ApplianceStatusCode';

/*  
*/
export class AppliancesServiceExportRequest {
 
    // Filter by folder Id (empty for no filter) 
    public folderId: string;
 
    // If true, search all appliances in folder and all its sub-folders 
    public subFolders: boolean;
 
    // Filter by name or mac address or serial number 
    public search: string;
 
    // Filter by appliance status(es) (empty or UNDEFINED means no filter) 
    public status: ApplianceStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(folderId?: string, subFolders?: boolean, search?: string, status?: ApplianceStatusCode[], sort?: string, format?: string, fields?: string[]) { 
        this.folderId = folderId;
        this.subFolders = subFolders;
        this.search = search;
        this.status = status;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


