import { SensorTypeCode } from '../enums/SensorTypeCode';
import { SensorStatusCode } from '../enums/SensorStatusCode';
import { StreamTypeCode } from '../enums/StreamTypeCode';

/*  
*/
export class SysSensorsCountRequest {
 
    // Filter by account Id (empty for no filter) 
    public accountId: string;
 
    // Filter by folder Id (empty for no filter) 
    public folderId: string;
 
    // If true, search all sensors in folder and all its sub-folders 
    public subFolders: boolean;
 
    // Filter by sensor type (empty or UNDEFINED for no filter) 
    public sensorType: SensorTypeCode;
 
    // Filter by sensor status (empty or UNDEFINED for no filter) 
    public status: SensorStatusCode;
 
    // Filter by stream type (empty or UNDEFINED for no filter) 
    public streamType: StreamTypeCode;
 
    constructor(accountId?: string, folderId?: string, subFolders?: boolean, sensorType?: SensorTypeCode, status?: SensorStatusCode, streamType?: StreamTypeCode) { 
        this.accountId = accountId;
        this.folderId = folderId;
        this.subFolders = subFolders;
        this.sensorType = sensorType;
        this.status = status;
        this.streamType = streamType;
    }

}


