
/*  
*/
export class AppliancesServiceStatusOvertimeRequest {
 
    // Appliance Id 
    public id: string;
 
    // Series start timestamp (Epoch millis) - 0 to set beginning according to the period 
    public from: number;
 
    // Series end timestamp (Epoch millis) - 0 to set current time 
    public to: number;
 
    constructor(id?: string, from?: number, to?: number) { 
        this.id = id;
        this.from = from;
        this.to = to;
    }

}


