/* 
   Severity type (level) code 
*/
export enum SeverityTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // No effect [1] 
    NO_EFFECT = 1,
 
    // Notification only [2] 
    NOTIFICATION = 2,
 
    // Lowest severity [3] 
    LOWEST = 3,
 
    // Low severity [4] 
    LOW = 4,
 
    // Medium severity [5] 
    MEDIUM = 5,
 
    // High severity [6] 
    HIGH = 6,
 
    // Highest severity [7] 
    HIGHEST = 7,
 
    // Critical severity [8] 
    CRITICAL = 8,
 
}