/* 
   Appliance command status 
*/
export enum CommandStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Pending command [1] 
    PENDING = 1,
 
    // Command in progress [2] 
    EXECUTING = 2,
 
    // Command completed successfully [3] 
    COMPLETED = 3,
 
    // Command failed [4] 
    FAILED = 4,
 
}