import { ObjectInstance } from '../common/ObjectInstance';

/* 
   Object Metadata info 
*/
export class ObjectInfo {
 
    // Object tracking Id (This is not unique id across the system) 
    public objectId: number;
 
    // Sensor id 
    public sensorId: string;
 
    // Instance time [Epoch milliseconds Timestamp] 
    public birthTime: number;
 
    // Instances of the object in the scene (trajectory) 
    public instances: ObjectInstance[];
 
    constructor(objectId?: number, sensorId?: string, birthTime?: number, instances?: ObjectInstance[]) { 
        this.objectId = objectId;
        this.sensorId = sensorId;
        this.birthTime = birthTime;
        this.instances = instances;
    }

}


