import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';

/* 
   List of filter criteria for conditional integration action 
*/
export class IntegrationActionFilter {
 
    // Activate integration only if the sensor id is in the list (empty list means no filter) 
    public sensorIds: string[];
 
    // Activate integration only if the rule id is in the list (empty list means no filter) 
    public ruleIds: string[];
 
    // Activate integration only if the object type code is in the list (empty list means no filter) 
    public objectTypes: ObjectTypeCode[];
 
    // Activate integration only if the object type code is in the list (empty list means no filter) 
    public behaviorTypes: BehaviorTypeCode[];
 
    constructor(sensorIds?: string[], ruleIds?: string[], objectTypes?: ObjectTypeCode[], behaviorTypes?: BehaviorTypeCode[]) { 
        this.sensorIds = sensorIds;
        this.ruleIds = ruleIds;
        this.objectTypes = objectTypes;
        this.behaviorTypes = behaviorTypes;
    }

}


