import { ProductTypeCode } from '../enums/ProductTypeCode';
import { DiskInfo } from '../common/DiskInfo';
import { DockerCredentials } from '../common/DockerCredentials';
import { ConfigurationVersion } from '../entities/ConfigurationVersion';

/* 
   Edge appliance capabilities full report <br>
   This structure describes the HW and SW spec of the device, most of the data is collected by the software and provided by the device<br>
   Some fields represents the device configuration (report intervals, docker credentials, components versions etc) and they are provided by the backend on device registration. 
*/
export class ApplianceCapabilities {
 
    // Product type code ((provided by the device, injected) 
    public productType: ProductTypeCode;
 
    // Appliance HW serial number (provided by the device) 
    public serialNumber: string;
 
    // Appliance HW model name (provided by the device) 
    public deviceModel: string;
 
    // Appliance CPU model name (provided by the device) 
    public cpuModel: string;
 
    // Appliance UPnP UUID (provided by the device) 
    public uuid: string;
 
    // Appliance IP address (provided by the device) 
    public ip: string;
 
    // Appliance Host name (provided by the device) 
    public host: string;
 
    // Docker engine version (provided by the device) 
    public dockerVersion: string;
 
    // Linux Kernel version (provided by the device) 
    public kernelVersion: string;
 
    // Total RAM size (bytes) (provided by the device) 
    public totalRam: number;
 
    // Disk info about all the disks installed (provided by the device) 
    public disks: DiskInfo[];
 
    // Command interval in seconds: how often to pull for commands (provided by the system)<br> The device shall provide a default value of 10, the system might provide different value 
    public commandInterval: number;
 
    // Status interval in seconds: how often to send status info (provided by the system)<br> The device shall provide a default value of 120 seconds, the system might provide different value 
    public statusInterval: number;
 
    // Docker repository credentials (provided by the system)<br> 
    public dockerCredentials: DockerCredentials;
 
    // SW Components configuration version (provided by the system)<br> This structure describes the SW components version to be deployed in the device 
    public configurationVersion: ConfigurationVersion;
 
    constructor(productType?: ProductTypeCode, serialNumber?: string, deviceModel?: string, cpuModel?: string, uuid?: string, ip?: string, host?: string, dockerVersion?: string, kernelVersion?: string, totalRam?: number, disks?: DiskInfo[], commandInterval?: number, statusInterval?: number, dockerCredentials?: DockerCredentials, configurationVersion?: ConfigurationVersion) { 
        this.productType = productType;
        this.serialNumber = serialNumber;
        this.deviceModel = deviceModel;
        this.cpuModel = cpuModel;
        this.uuid = uuid;
        this.ip = ip;
        this.host = host;
        this.dockerVersion = dockerVersion;
        this.kernelVersion = kernelVersion;
        this.totalRam = totalRam;
        this.disks = disks;
        this.commandInterval = commandInterval;
        this.statusInterval = statusInterval;
        this.dockerCredentials = dockerCredentials;
        this.configurationVersion = configurationVersion;
    }

}


