
/*  
*/
export class SchedulesServiceFindRequest {
 
    // Filter by name/description or partial name 
    public search: string;
 
    // Filter by folder Id 
    public folderId: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(search?: string, folderId?: string, sort?: string, page?: number, pageSize?: number) { 
        this.search = search;
        this.folderId = folderId;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


