
/*  
*/
export class AccountsServiceFindFoldersRequest {
 
    // Account Id 
    public id: string;
 
    // Filter by parent folder Id 
    public parentId: string;
 
    // Filter by name or partial name 
    public search: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(id?: string, parentId?: string, search?: string, sort?: string, page?: number, pageSize?: number) { 
        this.id = id;
        this.parentId = parentId;
        this.search = search;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


