import { TimeUnitCode } from '../enums/TimeUnitCode';
import { ApplianceStatusTimestamped } from '../common/ApplianceStatusTimestamped';

/* 
   Appliance status list over time 
*/
export class ApplianceStatusTimeSeries {
 
    // Time series label (optional) 
    public label: string;
 
    // Series tick interval (1: Second, 2: Minute, 3: Hour, 4: Day, 5: Week, 6: Month) 
    public interval: TimeUnitCode;
 
    // Time series representing the status KPIs per timestamp 
    public data: ApplianceStatusTimestamped[];
 
    constructor(label?: string, interval?: TimeUnitCode, data?: ApplianceStatusTimestamped[]) { 
        this.label = label;
        this.interval = interval;
        this.data = data;
    }

}


