
/* 
   Bounding Box of crop in a frame (inside 10000 x 10000 coordinate system) 
*/
export class BoundingBox {
 
    // Max X axis in the field-of-view of the bounding box 
    public maxX: number;
 
    // Max Y axis in the field-of-view of the bounding box 
    public maxY: number;
 
    // Min X axis in the field-of-view of the bounding box 
    public minX: number;
 
    // Min Y axis in the field-of-view of the bounding box 
    public minY: number;
 
    constructor(maxX?: number, maxY?: number, minX?: number, minY?: number) { 
        this.maxX = maxX;
        this.maxY = maxY;
        this.minX = minX;
        this.minY = minY;
    }

}


