import { Point } from '../common/Point';

/* 
   Geo Reference test used to test geo-referencing algorithm. The structure includes arbitrary list of points
   and the list of transformation coefficients. 
*/
export class GeoReferenceTest {
 
    // List of points in the FOV coordinate system (10000x10000) 
    public points: Point[];
 
    // List of transformation coefficients Transformation is based on number of coefficients: <br> For 8 coefficients (c[0] - c[7]) - projective transformation is used.<br> For 12 coefficients (c[0] - c[11]) - second order polynomial transformation is used.<br> 
    public coefficients: number[];
 
    constructor(points?: Point[], coefficients?: number[]) { 
        this.points = points;
        this.coefficients = coefficients;
    }

}


