import { SensorInfo } from '../common/SensorInfo';

/* 
   Group of sensor data with common attributes 
*/
export class SensorsGroup {
 
    // Account Id 
    public accountId: string;
 
    // Folder Id (empty for root folder) 
    public folderId: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Agent Id (Relevant for new pipeline only) 
    public agentId: string;
 
    // List of sensor configuration info 
    public sensors: SensorInfo[];
 
    constructor(accountId?: string, folderId?: string, applianceId?: string, agentId?: string, sensors?: SensorInfo[]) { 
        this.accountId = accountId;
        this.folderId = folderId;
        this.applianceId = applianceId;
        this.agentId = agentId;
        this.sensors = sensors;
    }

}


