import { Event } from '../entities/Event';

/* 
   @WebSocketMessage
   Payload for event notification socket message 
*/
export class SocketEventNotificationPayload {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Event info 
    public event: Event;
 
    constructor(code?: number, error?: string, event?: Event) { 
        this.code = code;
        this.error = error;
        this.event = event;
    }

}


