
/*  
*/
export class FeaturesServiceFindRequest {
 
    // Filter by name or full name or description 
    public search: string;
 
    // Filter by category 
    public category: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    constructor(search?: string, category?: string, sort?: string) { 
        this.search = search;
        this.category = category;
        this.sort = sort;
    }

}


