
/* 
   Bounding Box of an area in a map (in WGS-84 coordinate system) 
*/
export class BoundingMap {
 
    // Max latitude of the bounding box 
    public maxLat: number;
 
    // Max longitude of the bounding box 
    public maxLng: number;
 
    // Min latitude of the bounding box 
    public minLat: number;
 
    // Min longitude of the bounding box 
    public minLng: number;
 
    constructor(maxLat?: number, maxLng?: number, minLat?: number, minLng?: number) { 
        this.maxLat = maxLat;
        this.maxLng = maxLng;
        this.minLat = minLat;
        this.minLng = minLng;
    }

}


