import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { ApplianceProfile } from '../entities/ApplianceProfile';


/**
 * Services for custom appliance profiles actions
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class ApplianceProfilesService {

  // URL to web api
  private baseUrl = '/appliance-profiles';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new custom appliance profile
   * @Return: EntityResponse<ApplianceProfile>
   */
  create(body?: ApplianceProfile) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update custom appliance profile
   * @Return: EntityResponse<ApplianceProfile>
   */
  update(id?: string, body?: ApplianceProfile) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update Bulk of custom appliance profiles
   * @Return: ActionResponse
   */
  updateBulk(body?: ApplianceProfile[]) {
    return this.rest.put(`${this.baseUrl}/bulk`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete custom appliance profile from the system
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single profile by id
   * @Return: EntityResponse<ApplianceProfile>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find calendars by filters
   * @Return: EntitiesResponse<ApplianceProfile>
   */
  find(search?: string) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

}
