/* 
   Product type code 
*/
export enum ProductTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Virtual Edge (Pipeline running as Kubernetes deployment with no actual edge device) [1] 
    VIRTUAL = 1,
 
    // Custom Device (Customer provides it's own hardware) [2] 
    CUSTOM = 2,
 
    // Edge 200 [200] 
    EDGE_200 = 200,
 
    // Edge 220 [220] 
    EDGE_220 = 220,
 
    // Edge 250 [250] 
    EDGE_250 = 250,
 
    // Edge 320 [320] 
    EDGE_320 = 320,
 
    // Edge 325 [325] 
    EDGE_325 = 325,
 
    // Edge 500 [500] 
    EDGE_500 = 500,
 
    // Edge 550 [550] 
    EDGE_550 = 550,
 
}