import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { TimeUnitCode } from '../enums/TimeUnitCode';


/**
 * Services for event queries for system administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysEventsService {

  // URL to web api
  private baseUrl = '/sys/events';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Get single event by id
   * @Return: EntityResponse<Event>
   */
  get(id?: string, accountId?: string) {
    return this.rest.get(`${this.baseUrl}/${accountId}/${id}`);
  }

  /**
   * Find events by filters
   * @Return: QueryResponse<Event>
   */
  find(accountId?: string, folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}/${accountId}`, ...params);
  }

  /**
   * Get events count overtime for all events in the system
   * @Return: EntityResponse<EventCountTimeSeries>
   */
  getEventsCountOvertime(accountId?: string, folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], from?: number, to?: number, interval?: TimeUnitCode, format?: string) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (interval != null) { params.push(`interval=${interval}`); }
    if (format != null) { params.push(`format=${format}`); }

		return this.rest.get(`${this.baseUrl}/${accountId}/overtime`, ...params);
  }

}
