import { SensorTypeCode } from '../enums/SensorTypeCode';
import { SensorStatusCode } from '../enums/SensorStatusCode';
import { StreamTypeCode } from '../enums/StreamTypeCode';

/*  
*/
export class AppliancesServiceFindSensorsRequest {
 
    // Appliance Id 
    public id: string;
 
    // Filter by name or id or external id 
    public search: string;
 
    // Filter by sensor type(s) (empty or UNDEFINED means no filter) 
    public type: SensorTypeCode[];
 
    // Filter by sensor status(s) (empty or UNDEFINED means no filter) 
    public status: SensorStatusCode[];
 
    // Filter by sensor stream type(s) (empty or UNDEFINED means no filter) 
    public stream: StreamTypeCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(id?: string, search?: string, type?: SensorTypeCode[], status?: SensorStatusCode[], stream?: StreamTypeCode[], sort?: string, page?: number, pageSize?: number, format?: string, fields?: string[]) { 
        this.id = id;
        this.search = search;
        this.type = type;
        this.status = status;
        this.stream = stream;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
        this.format = format;
        this.fields = fields;
    }

}


