import { FovGeoAttributes } from '../common/FovGeoAttributes';

/*  
*/
export class SensorsServiceChangeFovRequest {
 
    // Sensor Id 
    public id: string;
 
    // Sensor azimuth (0 - 359) 
    public azimuth: number;
 
    // Sensor geo-location to update 
    public body: FovGeoAttributes;
 
    constructor(id?: string, azimuth?: number, body?: FovGeoAttributes) { 
        this.id = id;
        this.azimuth = azimuth;
        this.body = body;
    }

}


