import { StringIntValue } from '../common/StringIntValue';

/* 
   Distribution series result - keys are long values 
*/
export class DistributionOfString {
 
    // Label of series 
    public label: string;
 
    // Array of key-count values (key is string) 
    public data: StringIntValue[];
 
    constructor(label?: string, data?: StringIntValue[]) { 
        this.label = label;
        this.data = data;
    }

}


