
/* 
   Sensor training analysis result for a single model
   This data model is used by the training process of Anomaly Detection 
*/
export class AnalysisResult {
 
    // Results model type 
    public modelType: string;
 
    // Path to the results model file 
    public resultsPath: string;
 
    // When the training session was completed [Epoch milliseconds Timestamp] 
    public completedOn: number;
 
    constructor(modelType?: string, resultsPath?: string, completedOn?: number) { 
        this.modelType = modelType;
        this.resultsPath = resultsPath;
        this.completedOn = completedOn;
    }

}


