import { ObjectTypeCode } from '../enums/ObjectTypeCode';

/* 
   Used to reflect object type hierarchy 
*/
export class ObjectTypeNode {
 
    // Object type enum name 
    public name: string;
 
    // Object type enum value 
    public objectTypeCode: ObjectTypeCode;
 
    // Children (sub types) 
    public children: ObjectTypeNode[];
 
    constructor(name?: string, objectTypeCode?: ObjectTypeCode, children?: ObjectTypeNode[]) { 
        this.name = name;
        this.objectTypeCode = objectTypeCode;
        this.children = children;
    }

}


