import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';



/**
 * Audit log queries for account administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class AuditLogService {

  // URL to web api
  private baseUrl = '/auditlog';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Find list of audit log entries and filter
   * @Return: QueryResponse<AuditLog>
   */
  find(userId?: string, from?: number, to?: number, itemType?: string, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (userId != null) { params.push(`userId=${userId}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (itemType != null) { params.push(`itemType=${itemType}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Export list of audit log entries and filter
   * @Return: StreamContent
   */
  exportFormat(userId?: string, from?: number, to?: number, itemType?: string, sort?: string, format?: string, fields?: string[]) {
    const params = new Array();		
    if (userId != null) { params.push(`userId=${userId}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (itemType != null) { params.push(`itemType=${itemType}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }

		return this.rest.download(`auditlog`,`${this.baseUrl}/export`, ...params);
  }

  /**
   * Get single audit log entry by id
   * @Return: EntityResponse<Appliance>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

}
