import { AnalysisResult } from '../common/AnalysisResult';
import { SensorAnomalyRuleInfo } from '../common/SensorAnomalyRuleInfo';

/* 
   Sensor anomaly information
   This data model is used by the inference process of Anomaly Detection 
*/
export class SensorAnomalyInfo {
 
    // Sensor Id 
    public sensorId: string;
 
    // List of results (model + file path) 
    public results: AnalysisResult[];
 
    // Anomaly rule info 
    public ruleInfo: SensorAnomalyRuleInfo;
 
    constructor(sensorId?: string, results?: AnalysisResult[], ruleInfo?: SensorAnomalyRuleInfo) { 
        this.sensorId = sensorId;
        this.results = results;
        this.ruleInfo = ruleInfo;
    }

}


