import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { TimeUnitCode } from '../enums/TimeUnitCode';
import { Event } from '../entities/Event';
import { EventStatusCode } from '../enums/EventStatusCode';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';


/**
 * Services for events actions
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class EventsService {

  // URL to web api
  private baseUrl = '/events';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new event
   * @Return: EntityResponse<Event>
   */
  create(body?: Event) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change event workflow status
   * @Return: EntityResponse<Event>
   */
  changeStatus(id?: string, status?: EventStatusCode) {
    return this.rest.put(`${this.baseUrl}/${id}/status/${status}`, null);
  }

  /**
   * Set event image path
   * @Return: EntityResponse<Event>
   */
  setImagePath(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/imagePath`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Set event clip path
   * @Return: EntityResponse<Event>
   */
  setClipPath(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/clipPath`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Get single event by id
   * @Return: EntityResponse<Event>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get events by list of ids
   * @Return: EntitiesResponse<Event>
   */
  list(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.get(`${this.baseUrl}/list`, ...params);
  }

  /**
   * Find events by filters
   * @Return: QueryResponse<Event>
   */
  find(folderId?: string, subFolders?: boolean, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Export list of events and filter
   * @Return: StreamContent
   */
  exportFormat(folderId?: string, subFolders?: boolean, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, sort?: string, format?: string, fields?: string[]) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }

		return this.rest.download(`events`,`${this.baseUrl}/export`, ...params);
  }

  /**
   * Find events generated in area using spatial query
   * @Return: QueryResponse<Event>
   */
  findInArea(folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.post(`${this.baseUrl}/area`, null, ...params);
  }

  /**
   * Get event image [response content type: image/jpeg]
   * @Return: StreamingOutput of the image
   */
  getEventImage(id?: string) {
    return this.rest.download(`events`,`${this.baseUrl}/${id}/image`);
  }

  /**
   * Get event clip [response content type: video/mp4]
   * @Return: StreamingOutput of the clip
   */
  getEventClip(id?: string) {
    return this.rest.download(`events`,`${this.baseUrl}/${id}/clip`);
  }

  /**
   * Get events count overtime for all events in the system
   * @Return: EntityResponse<EventCountTimeSeries>
   */
  getEventsCountOvertime(folderId?: string, sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, interval?: TimeUnitCode, labelFormat?: string) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (rule != null) { params.push(`rule=${rule}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (interval != null) { params.push(`interval=${interval}`); }
    if (labelFormat != null) { params.push(`labelFormat=${labelFormat}`); }

		return this.rest.get(`${this.baseUrl}/overtime`, ...params);
  }

}
