import { Threshold } from '../common/Threshold';

/* 
   Thresholds list warning state (yellow) and error state (red) health thresholds 
*/
export class Thresholds {
 
    // Warning state thresholds before generating health alert 
    public warningThreshold: Threshold;
 
    // Error state thresholds before generating health alert 
    public errorThreshold: Threshold;
 
    constructor(warningThreshold?: Threshold, errorThreshold?: Threshold) { 
        this.warningThreshold = warningThreshold;
        this.errorThreshold = errorThreshold;
    }

}


