
/*  
*/
export class SysKeysServiceCreateTokenRequest {
 
    // Account id 
    public accountId: string;
 
    // User id 
    public userId: string;
 
    // Expiration time (in milliseconds) 
    public ttl: number;
 
    constructor(accountId?: string, userId?: string, ttl?: number) { 
        this.accountId = accountId;
        this.userId = userId;
        this.ttl = ttl;
    }

}


