import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { TimeUnitCode } from '../enums/TimeUnitCode';

/*  
*/
export class SearchEventCountRequest {
 
    // Session Id 
    public id: string;
 
    // Filter by sensor Id(s) (empty for no filter) - multiple values enabled 
    public sensorId: string[];
 
    // Filter by object type(s) (empty for no filter) - multiple values enabled 
    public objectType: ObjectTypeCode[];
 
    // Filter search events after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter search events before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    // Time series interval unit (MINUTE, HOUR, DAY, MINUTE) 
    public interval: TimeUnitCode;
 
    // Label format of Y axis 
    public labelFormat: string;
 
    constructor(id?: string, sensorId?: string[], objectType?: ObjectTypeCode[], from?: number, to?: number, interval?: TimeUnitCode, labelFormat?: string) { 
        this.id = id;
        this.sensorId = sensorId;
        this.objectType = objectType;
        this.from = from;
        this.to = to;
        this.interval = interval;
        this.labelFormat = labelFormat;
    }

}


