
/* 
   Coordinate of WGS-84 grid system
   See (https://en.wikipedia.org/wiki/World_Geodetic_System) 
*/
export class Coordinate {
 
    // Latitude 
    public lat: number;
 
    // Longitude 
    public lon: number;
 
    // Altitude 
    public alt: number;
 
    constructor(latitude?: number, longitude?: number, altitude?: number) { 
        this.lat = latitude;
        this.lon = longitude;
        this.alt = altitude;
    }

}


