import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';

/*  
*/
export class RulesServiceExportRequest {
 
    // Filter by folder (empty for no filter) 
    public folderId: string;
 
    // Filter by sensor Id (empty for no filter) 
    public sensorId: string;
 
    // Filter by name or description or external id or partial (using * wildcard) 
    public search: string;
 
    // Filter by behavior type(s) (empty for no filter) 
    public behaviorType: BehaviorTypeCode[];
 
    // Filter by severity(ies) (empty for no filter) 
    public severity: SeverityTypeCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(folderId?: string, sensorId?: string, search?: string, behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], sort?: string, format?: string, fields?: string[]) { 
        this.folderId = folderId;
        this.sensorId = sensorId;
        this.search = search;
        this.behaviorType = behaviorType;
        this.severity = severity;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


