import { AgentStatusCode } from '../enums/AgentStatusCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Agent represents the new edge analytics pipeline (including former agent + AS + CS in a single process)
   Agent can process video from multiple sensors. Agent is hosted in the edge device (appliance) as single Docker container.
   A single appliance can host multiple agents (container instances of the same Docker image) 
*/
export class Agent extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Appliance Id of the appliance hosting the agent 
    public applianceId: string;
 
    // Agent status 
    public status: AgentStatusCode;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


