/* 
   Event status code 
*/
export enum EventStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // New event [1] 
    NEW = 1,
 
    // Open (in-progress) event [2] 
    OPEN = 2,
 
    // Closed event (tagged as True event) [6] 
    CLOSED_TRUE = 6,
 
    // Closed event (tagged as False event) [7] 
    CLOSED_FALSE = 7,
 
    // Relevant anomaly event [8] 
    CLOSED_RELEVANT = 8,
 
    // Irrelevant anomaly event [9] 
    CLOSED_IRRELEVANT = 9,
 
}