
/* 
   Status of a search session 
*/
export class SearchStatus {
 
    // Search session id 
    public sessionId: string;
 
    // True if the query is done and all results collected 
    public isFinished: boolean;
 
    // Total events count so far 
    public total: number;
 
    // The current search session progress (0-100) 
    public progress: number;
 
    constructor(sessionId?: string, isFinished?: boolean, total?: number, progress?: number) { 
        this.sessionId = sessionId;
        this.isFinished = isFinished;
        this.total = total;
        this.progress = progress;
    }

}


