import { LineCrossDirectionCode } from '../enums/LineCrossDirectionCode';
import { Point } from '../common/Point';
import { RulePolygon } from '../common/RulePolygon';

/* 
   Search shape definition for Sensors or GEO search 
*/
export class SearchShape extends RulePolygon {
 
    // Sensor Id in case sourceScope is SENSOR or 'GEO' in case sourceScope is GEO 
    public sensorId: string;
 
    // Line crossing direction (used only in case of line-crossing rule type) 
    public lineCrossDir: LineCrossDirectionCode;
 
    // Polygon points 
    public points: Point[];
 

}


