
/*  
*/
export class FoldersServiceExportRequest {
 
    // Folder Id 
    public id: string;
 
    // Filter by name or partial name 
    public search: string;
 
    // Filter by parent folder Id 
    public parentId: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(id?: string, search?: string, parentId?: string, sort?: string, format?: string, fields?: string[]) { 
        this.id = id;
        this.search = search;
        this.parentId = parentId;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


