import { TimeUnitCode } from '../enums/TimeUnitCode';
import { ApplianceKpiTimestamped } from '../common/ApplianceKpiTimestamped';

/* 
   Appliance KPIs list over time (CPU, RAM, LOAD) 
*/
export class ApplianceKpiTimeSeries {
 
    // Time series label (optional) 
    public label: string;
 
    // Series tick interval (1: Second, 2: Minute, 3: Hour, 4: Day, 5: Week, 6: Month) 
    public interval: TimeUnitCode;
 
    // Time series representing the KPIs per timestamp 
    public data: ApplianceKpiTimestamped[];
 
    constructor(label?: string, interval?: TimeUnitCode, data?: ApplianceKpiTimestamped[]) { 
        this.label = label;
        this.interval = interval;
        this.data = data;
    }

}


