import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { Rule } from '../entities/Rule';


/**
 * Services for rules actions
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class RulesService {

  // URL to web api
  private baseUrl = '/rules';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new rule
   * @Return: EntityResponse<Rule>
   */
  create(body?: Rule) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update rule
   * @Return: EntityResponse<Rule>
   */
  update(id?: string, body?: Rule) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Enable list of rules
   * @Return: ActionResponse
   */
  enable(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.put(`${this.baseUrl}/enable`, null, ...params);
  }

  /**
   * Disable list of rules
   * @Return: ActionResponse
   */
  disable(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.put(`${this.baseUrl}/disable`, null, ...params);
  }

  /**
   * Delete rule from the system
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single rule by id
   * @Return: EntityResponse<Rule>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get rules by list of ids
   * @Return: EntitiesResponse<Rule>
   */
  list(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.get(`${this.baseUrl}/list`, ...params);
  }

  /**
   * Find rules by filters
   * @Return: QueryResponse<Rule>
   */
  find(folderId?: string, sensorId?: string, search?: string, behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Export list of rules and filter
   * @Return: StreamContent
   */
  exportFormat(folderId?: string, sensorId?: string, search?: string, behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], sort?: string, format?: string, fields?: string[]) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (behaviorType != null) { params.push(`behaviorType=${behaviorType}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }

		return this.rest.download(`rules`,`${this.baseUrl}/export`, ...params);
  }

  /**
   * Find anomaly rules by filters
   * @Return: QueryResponse<Rule>
   */
  findAnomalyRules(folderId?: string, sensorId?: string, search?: string, severity?: SeverityTypeCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (severity != null) { params.push(`severity=${severity}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}/anomaly`, ...params);
  }

  /**
   * Get rules specifications available by account features
   * @Return: EntitiesResponse<RuleSpec>
   */
  getSpecs() {
    return this.rest.get(`${this.baseUrl}/spec`);
  }

}
