import { CommandStatusCode } from '../enums/CommandStatusCode';

/*  
*/
export class SysAppliancesServiceGetCommandsRequest {
 
    // Filter by account Id (empty for no filter) 
    public accountId: string;
 
    // Filter by appliance Id (empty for no filter) 
    public applianceId: string;
 
    // Filter by command status(es) (empty or UNDEFINED means no filter) 
    public status: CommandStatusCode[];
 
    constructor(accountId?: string, applianceId?: string, status?: CommandStatusCode[]) { 
        this.accountId = accountId;
        this.applianceId = applianceId;
        this.status = status;
    }

}


