import { ApplianceKpiDataPoint } from '../common/ApplianceKpiDataPoint';

/* 
   Data point in a series representing appliance KPI values in a specific timestamp 
*/
export class ApplianceKpiTimestamped {
 
    // Timestamp (Epoch milliseconds) 
    public timestamp: number;
 
    // Data point label 
    public label: string;
 
    // Appliance KPIs 
    public value: ApplianceKpiDataPoint;
 
    constructor(timestamp?: number, label?: string, value?: ApplianceKpiDataPoint) { 
        this.timestamp = timestamp;
        this.label = label;
        this.value = value;
    }

}


