import { AccountTypeCode } from '../enums/AccountTypeCode';
import { AccountStatusCode } from '../enums/AccountStatusCode';

/*  
*/
export class AccountsServiceExportRequest {
 
    // Filter by name or partial name 
    public name: string;
 
    // Filter by type(s) (empty or UNDEFINED means no filter) 
    public type: AccountTypeCode[];
 
    // Filter by status(s) (empty or UNDEFINED means no filter) 
    public status: AccountStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(name?: string, type?: AccountTypeCode[], status?: AccountStatusCode[], sort?: string, format?: string, fields?: string[]) { 
        this.name = name;
        this.type = type;
        this.status = status;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


