import { TimeUnitCode } from '../enums/TimeUnitCode';
import { EventCountDataPoint } from '../common/EventCountDataPoint';

/* 
   Event count time series result 
*/
export class EventCountTimeSeries {
 
    // Label of series 
    public label: string;
 
    // Series tick interval (1: Second, 2: Minute, 3: Hour, 4: Day, 5: Week, 6: Month) 
    public interval: TimeUnitCode;
 
    // Time series representing the data points over time 
    public data: EventCountDataPoint[];
 
    constructor(label?: string, interval?: TimeUnitCode, data?: EventCountDataPoint[]) { 
        this.label = label;
        this.interval = interval;
        this.data = data;
    }

}


