import { Coordinate } from '../common/Coordinate';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';
import { EventStatusCode } from '../enums/EventStatusCode';

/*  
*/
export class EventsServiceFindInAreaRequest {
 
    // Filter by folder (empty for no filter) 
    public folderId: string;
 
    // Polygon representing the search area @QBodyParam 
    public searchArea: Coordinate[];
 
    // Filter by sensor Id (empty for no filter) 
    public sensorId: string;
 
    // Filter by object type(s) (empty for no filter) - multiple values enabled 
    public objectType: ObjectTypeCode[];
 
    // Filter by behavior type(s) (empty for no filter) - multiple values enabled 
    public behaviorType: BehaviorTypeCode[];
 
    // Filter by severity type(s) (empty for no filter) - multiple values enabled 
    public severity: SeverityTypeCode[];
 
    // Filter by event status(es) (empty for no filter) - multiple values enabled 
    public status: EventStatusCode[];
 
    // Filter by event rule(s) (empty for no filter) - multiple values enabled 
    public rule: string[];
 
    // Filter events after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter events before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(folderId?: string, searchArea?: Coordinate[], sensorId?: string, objectType?: ObjectTypeCode[], behaviorType?: BehaviorTypeCode[], severity?: SeverityTypeCode[], status?: EventStatusCode[], rule?: string[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) { 
        this.folderId = folderId;
        this.searchArea = searchArea;
        this.sensorId = sensorId;
        this.objectType = objectType;
        this.behaviorType = behaviorType;
        this.severity = severity;
        this.status = status;
        this.rule = rule;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


