/* 
   Integration status code 
*/
export enum IntegrationStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Some of the integrations succeeded but not all [1] 
    PARTIAL = 1,
 
    // All the integrations succeeded [2] 
    SUCCESS = 2,
 
    // All the integrations failed [3] 
    FAILED = 3,
 
}