import { BaseEntity } from '../entities/BaseEntity';
import { IntegrationActionFilter } from '../entities/IntegrationActionFilter';

/* 
   Integration action describes content details to use for a specific integration target 
*/
export class IntegrationAction extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Folder Id (Empty if integration is associated to the account level) 
    public folderId: string;
 
    // Integration name 
    public name: string;
 
    // Integration target Id 
    public targetId: string;
 
    // List of recipients (applicable for SMTP/EMAIL protocol) 
    public recipients: string[];
 
    // Message subject template (applicable for SMTP/EMAIL protocol) 
    public subject: string;
 
    // Message body template 
    public body: string;
 
    // Message content mime type 
    public mimeType: string;
 
    // Is the integration action enabled? 
    public enabled: boolean;
 
    // Filter criteria for conditional integration 
    public filter: IntegrationActionFilter;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


