import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { SearchColor } from '../entities/SearchColor';

/* 
   Object attributes to search for 
*/
export class SearchObject {
 
    // Object type to search for: PERSON | CAR | MOTORCYCLE ... 
    public type: ObjectTypeCode;
 
    // Optional object colors to search for 
    public colors: SearchColor[];
 
    constructor(type?: ObjectTypeCode, colors?: SearchColor[]) { 
        this.type = type;
        this.colors = colors;
    }

}


