/* 
   Agent (New Pipeline) status code 
*/
export enum AgentStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Agent is running [1] 
    ACTIVE = 1,
 
    // Agent in Warning state [2] 
    WARNING = 2,
 
    // Agent in Error state [3] 
    ERROR = 3,
 
    // Agent is not active [4] 
    INACTIVE = 4,
 
    // Agent was added but did not establish connection yet [5] 
    PENDING = 5,
 
}