import { BaseEntity } from '../entities/BaseEntity';

/* 
   Account statistics collect monthly statistics (e.g. number of cameras) 
*/
export class AccountStatistics extends BaseEntity {
 
    // Account name 
    public name: string;
 
    // Account Id 
    public accountId: string;
 
    // Month Id (number in the format of YYYYMM) 
    public monthId: number;
 
    // Total number of channels (sensors) 
    public sensors: number;
 
    // Total number of edge devices (appliances) 
    public devices: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


