import { EventStatusCode } from '../enums/EventStatusCode';

/*  
*/
export class AnomalyServiceFindEventsRequest {
 
    // Filter by account Id 
    public accountId: string;
 
    // Filter by sensor Id 
    public sensorId: string;
 
    // Filter by event status (empty for no filter) 
    public status: EventStatusCode;
 
    // Filter events after the provided timestamp (epoch milliseconds) 
    public from: number;
 
    // Filter events before the provided timestamp (epoch milliseconds) 
    public to: number;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(accountId?: string, sensorId?: string, status?: EventStatusCode, from?: number, to?: number, sort?: string, page?: number, pageSize?: number) { 
        this.accountId = accountId;
        this.sensorId = sensorId;
        this.status = status;
        this.from = from;
        this.to = to;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


