import { Coordinate } from '../common/Coordinate';
import { Dimension } from '../common/Dimension';
import { TransformationTypeCode } from '../enums/TransformationTypeCode';
import { GeoControlPoint } from '../common/GeoControlPoint';

/* 
   FOV (Sensor Field of View) Geo attributes 
*/
export class FovGeoAttributes {
 
    // Sensor FOV geo area polygon (the bounding polygon of the area covered by the FOV) 
    public geoArea: Coordinate[];
 
    // The normalized (10000 x 10000) visible area (rectangle) in the FOV 
    public visibleArea: Dimension;
 
    // Geo referencing transformation algorithm 
    public transformType: TransformationTypeCode;
 
    // Coefficient matrix for the FOV - for 8 coefficients projective transformation is used, for 12 coefficients polynomial transformation is used 
    public coefficients: number[];
 
    // Min X axis in the field-of-view of the bounding box 
    public controlPoints: GeoControlPoint[];
 
    constructor(geoArea?: Coordinate[], visibleArea?: Dimension, transformType?: TransformationTypeCode, coefficients?: number[], controlPoints?: GeoControlPoint[]) { 
        this.geoArea = geoArea;
        this.visibleArea = visibleArea;
        this.transformType = transformType;
        this.coefficients = coefficients;
        this.controlPoints = controlPoints;
    }

}


