import { BaseEntity } from '../entities/BaseEntity';
import { EntityTypeCode } from '../enums/EntityTypeCode';

/* 
   Describes status of integration attempt (for events and health integrations) 
*/
export class IntegrationStatus extends BaseEntity {
 
    // Source of health event (can be only EVENT or HEALTH_EVENT) 
    public source: EntityTypeCode;
 
    // Account Id 
    public accountId: string;
 
    // Entity Id of the event or health event (depends on source tye) 
    public entityId: string;
 
    // Integration action Id 
    public integrationActionId: string;
 
    // Integration try number 
    public tryNumber: number;
 
    // Error message in case of failure 
    public error: string;
 
    // Integration try success flag 
    public success: boolean;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


