
/* 
   Event count time series data point 
*/
export class EventCountDataPoint {
 
    // Timestamp (Epoch milliseconds) 
    public timestamp: number;
 
    // Data point label 
    public label: string;
 
    // Data point value 
    public value: number;
 
    constructor(timestamp?: number, label?: string, value?: number) { 
        this.timestamp = timestamp;
        this.label = label;
        this.value = value;
    }

}


