import { ApplianceStatusCode } from '../enums/ApplianceStatusCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Appliance status - reported periodically by the appliance 
*/
export class ApplianceStatus extends BaseEntity {
 
    // Appliance Id 
    public applianceId: string;
 
    // Account Id 
    public accountId: string;
 
    // Appliance status 
    public status: ApplianceStatusCode;
 
    // Average CPU usage percentage [1-100] 
    public cpuAvg: number;
 
    // Max CPU usage percentage [1-100] 
    public cpuMax: number;
 
    // RAM usage percentage [1-100] 
    public ram: number;
 
    // Load average percentage (could be more than 100, e.g. load average of 2.21 will be represented as 221) 
    public loadAvg: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


