import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { SearchDefinition } from '../entities/SearchDefinition';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { TimeUnitCode } from '../enums/TimeUnitCode';


/**
 * Services for metadata search actions
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SearchService {

  // URL to web api
  private baseUrl = '/search';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new search definition
   * @Return: EntityResponse<SearchDefinition>
   */
  create(body?: SearchDefinition) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update search definition
   * @Return: EntityResponse<SearchDefinition>
   */
  update(id?: string, body?: SearchDefinition) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Delete search definition from the system
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get single search definition by id
   * @Return: EntityResponse<SearchDefinition>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get multiple search definitions by list of ids
   * @Return: EntitiesResponse<SearchDefinition>
   */
  list(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.get(`${this.baseUrl}/list`, ...params);
  }

  /**
   * Find search definitions by filters
   * @Return: QueryResponse<SearchDefinition>
   */
  find(search?: string, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Perform metadata search by the search definition
   * @Return: EntityResponse<SearchStatus>
   * Search image web socket (/ws/search?search=searchId) must be open before this method invocation.
   * Please provide search id to query definition and use the same id for the web socket
   */
  execute(body?: SearchDefinition) {
    return this.rest.post(`${this.baseUrl}/execute`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Perform metadata saved search by the search id
   * @Return: EntityResponse<SearchStatus>
   */
  executeById(id?: string) {
    return this.rest.get(`${this.baseUrl}/execute/${id}`);
  }

  /**
   * Get search session status
   * @Return: EntityResponse<SearchStatus>
   */
  getSearchStatus(id?: string) {
    return this.rest.get(`${this.baseUrl}/sessions/${id}/status`);
  }

  /**
   * Cancel search session
   * @Return: ActionResponse
   */
  cancelSearchSession(id?: string) {
    return this.rest.delete(`${this.baseUrl}/sessions/${id}`);
  }

  /**
   * Get single search event item by id and sessionId
   * @Return: EntityResponse<SearchDefinition>
   */
  getEvent(id?: string, eventId?: string) {
    return this.rest.get(`${this.baseUrl}/sessions/${id}/event/${eventId}`);
  }

  /**
   * Get search event image [response content type: image/jpeg]
   * @Return: StreamingOutput of the image
   */
  getEventImage(id?: string, eventId?: string) {
    return this.rest.download(`search`,`${this.baseUrl}/sessions/${id}/image/${eventId}`);
  }

  /**
   * Find list of search events by filter
   * @Return: QueryResponse<SearchDefinition>
   */
  findEvents(id?: string, sensorId?: string[], objectType?: ObjectTypeCode[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}/sessions/${id}/find`, ...params);
  }

  /**
   * Get total search events count by filter
   * @Return: QueryResponse<SearchDefinition> entities list is null
   */
  totalEvents(id?: string, sensorId?: string[], objectType?: ObjectTypeCode[], from?: number, to?: number, sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}/sessions/${id}/total`, ...params);
  }

  /**
   * Export list of search events by filter
   * @Return: StreamContent
   */
  exportEvents(id?: string, sensorId?: string[], objectType?: ObjectTypeCode[], from?: number, to?: number, sort?: string, format?: string, fields?: string[]) {
    const params = new Array();		
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }

		return this.rest.download(`search`,`${this.baseUrl}/sessions/${id}/export`, ...params);
  }

  /**
   * Get events count overtime for all events in the system
   * @Return: EntityResponse<EventCountTimeSeries>
   */
  getEventsCountOvertime(id?: string, sensorId?: string[], objectType?: ObjectTypeCode[], from?: number, to?: number, interval?: TimeUnitCode, labelFormat?: string) {
    const params = new Array();		
    if (sensorId != null) { params.push(`sensorId=${sensorId}`); }
    if (objectType != null) { params.push(`objectType=${objectType}`); }
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }
    if (interval != null) { params.push(`interval=${interval}`); }
    if (labelFormat != null) { params.push(`labelFormat=${labelFormat}`); }

		return this.rest.get(`${this.baseUrl}/sessions/${id}/overtime`, ...params);
  }

}
