import { SensorStatus } from '../entities/SensorStatus';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any sensor health status change in the registered account 
*/
export class SocketHealthStatusNotificationPayload {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Sensor status 
    public sensorStatus: SensorStatus;
 
    constructor(code?: number, error?: string, sensorStatus?: SensorStatus) { 
        this.code = code;
        this.error = error;
        this.sensorStatus = sensorStatus;
    }

}


