import { BaseEntity } from '../entities/BaseEntity';
import { AccountTypeCode } from '../enums/AccountTypeCode';
import { AccountStatusCode } from '../enums/AccountStatusCode';
import { Coordinate } from '../common/Coordinate';
import { AccountSettings } from '../common/AccountSettings';
import { HealthThresholds } from '../common/HealthThresholds';

/* 
   Account entity in the system represents customer account which groups set of folders, cameras, rules, integrations and customer specific operational configuration 
*/
export class Account extends BaseEntity {
 
    // Account name 
    public name: string;
 
    // Account key 
    public key: string;
 
    // Account Type: UNDEFINED | DEMO | TRIAL | PARTNER | CUSTOMER 
    public type: AccountTypeCode;
 
    // Account status: UNDEFINED | ACTIVE | SUSPENDED 
    public status: AccountStatusCode;
 
    // When the account was suspended [Epoch milliseconds Timestamp] second inline field documentation 
    public suspendedOn: number;
 
    // List of associated account groups (for feature toggle) 
    public groups: string[];
 
    // Account geo area polygon (the bounding polygon of the area) 
    public geoArea: Coordinate[];
 
    // Account geo location (the center coordinate of the account region) 
    public geoLocation: Coordinate;
 
    // Account timezone (IANA format, default is UTC) 
    public timezone: string;
 
    // When the account will be expired [Epoch milliseconds Timestamp] (0 means no expiration time) 
    public expiredOn: number;
 
    // Limit the number of channels per account (0 means no limit) 
    public maxChannels: number;
 
    // Account description 
    public description: string;
 
    // Account specific settings 
    public settings: AccountSettings;
 
    // Health notifications thresholds for sensors and appliances 
    public healthThresholds: HealthThresholds;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


