
/*  
*/
export class AuditLogServiceFindRequest {
 
    // Filter by user id (empty means no filter) 
    public userId: string;
 
    // Filter by time period (lower bound in epoch time milliseconds) - 0 for no filter 
    public from: number;
 
    // Filter by time period (upper bound in epoch time milliseconds) - 0 for no filter 
    public to: number;
 
    // Filter by item type or partial type - empty for no filter 
    public itemType: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(userId?: string, from?: number, to?: number, itemType?: string, sort?: string, page?: number, pageSize?: number) { 
        this.userId = userId;
        this.from = from;
        this.to = to;
        this.itemType = itemType;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


