import { EventStatusCode } from '../enums/EventStatusCode';

/* 
   Subset of event info required by the anomaly service 
*/
export class AnomalyEventInfo {
 
    // Event Id 
    public eventId: string;
 
    // List of detected object Ids 
    public objectIds: number[];
 
    // Validation status for event processing workflow: NEW | OPEN | CLOSE | IRRELEVANT | FALSE 
    public status: EventStatusCode;
 
    constructor(eventId?: string, objectIds?: number[], status?: EventStatusCode) { 
        this.eventId = eventId;
        this.objectIds = objectIds;
        this.status = status;
    }

}


