import { Coordinate } from '../common/Coordinate';

/* 
   Geo Reference data includes the contour of the raster image on the World coordinate system (WGS-84)
   and the list of transformation coefficients. 
*/
export class GeoReferenceData {
 
    // List of WGS-84 coordinates (contour of the FOV on map)<br> The order of the coordinates corresponds to the FOV corners: <ul> <li>coordinates[0] = FOV ULC (Upper Left Corner:  0, 0)</li> <li>coordinates[1] = FOV URC (Upper Right Corner: 10000, 0)</li> <li>coordinates[2] = FOV LRC (Lower Right Corner: 10000, 10000)</li> <li>coordinates[3] = FOV LLC (Lower Left Corner:  0, 10000)</li> </ul> 
    public coordinates: Coordinate[];
 
    // List of transformation coefficients matrix <br> Transformation is based on number of coefficients: <br> For 8 coefficients (c[0] - c[7]) - projective transformation is used.<br> In this case the transformation formula is: <blockquote> <pre> X' = (c[0]*x + c[1]*y + c[2]) / (c[6]*x + c[7]*y + 1) Y' = (c[3]*x + c[4]*y + c[5]) / (c[6]*x + c[7]*y + 1) where X' is WGS84 Longitude and Y' is WGS84 Latitude </pre> </blockquote> <br> For 12 coefficients (c[0] - c[11]) - second order polynomial transformation is used.<br> In this case the transformation formula is: <blockquote> <pre> X' = c[0] + c[1]*x + c[2]*y + c[3]*x*y + c[4]*x*x + c[5]*y*y Y' = c[6] + c[7]*x + c[8]*y + c[9]*x*y + c[10]*x*x + c[11]*y*y where X' is WGS84 Longitude and Y' is WGS84 Latitude </pre> </blockquote> If controlPoints array includes 6 sets of reference points, the result will be based on second order Polynomial Transformation that consists of 12 coefficients. The transform function is: X' = c[0] + c[1]*x + c[2]*y + c[3]*x*y + c[4]*x*x + c[5]*y*y Y' = c[6] + c[7]*x + c[8]*y + c[9]*x*y + c[10]*x*x + c[11]*y*y 
    public coefficients: number[];
 
    constructor(coordinates?: Coordinate[], coefficients?: number[]) { 
        this.coordinates = coordinates;
        this.coefficients = coefficients;
    }

}


