import { BaseEntity } from '../entities/BaseEntity';

/* 
   Sensor preset represent specific sensor FOV attributes.
   In a fixed camera there is only one preset (0), in a PTZ cameras, user can specify multiple FOVs each has its own preset 
*/
export class Preset extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Sensor Id 
    public sensorId: string;
 
    // Preset number (0 for default) 
    public presetNum: number;
 
    // Reference Image Url 
    public refImageUrl: string;
 
    // Sensor preset calibration info 
    public calibration: any;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


