/* 
   Detected color of object (or partial object) 
*/
export enum ColorCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // No color [1] 
    NONE = 1,
 
    // Unknown color [2] 
    UNKNOWN = 2,
 
    // Multiple colors [4] 
    MULTICOLORED = 4,
 
    // White [8] 
    WHITE = 8,
 
    // Black [16] 
    BLACK = 16,
 
    // Gray [32] 
    GRAY = 32,
 
    // Blue [64] 
    BLUE = 64,
 
    // Brown [128] 
    BROWN = 128,
 
    // Green [256] 
    GREEN = 256,
 
    // Yellow [512] 
    YELLOW = 512,
 
    // Orange [1024] 
    ORANGE = 1024,
 
    // Red [2048] 
    RED = 2048,
 
    // Pink [4096] 
    PINK = 4096,
 
    // Purple [8192] 
    PURPLE = 8192,
 
    // Cyan [16384] 
    CYAN = 16384,
 
    // Silver [32768] 
    SILVER = 32768,
 
}