import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { SearchShape } from '../entities/SearchShape';

/* 
   Search behavior description 
*/
export class SearchBehavior {
 
    // Rule behavior type: MOVING | CROSSING | OCCUPANCY ... 
    public behaviorType: BehaviorTypeCode;
 
    // Dwell time in seconds, required for certain behaviors (MOVING | OCCUPANCY | GROUPING) 
    public dwellTime: number;
 
    // Min number of people in group, required for certain behaviors (OCCUPANCY | GROUPING) 
    public minObjectsInGroup: number;
 
    // Member distance in group, required for GROUPING 
    public clusterDistance: number;
 
    // List of search shapes on FOV (in case sourceScope is SENSOR) or a single shape on world coordinates (in case sourceScope is GEO) 
    public shapes: SearchShape[];
 
    constructor(behaviorType?: BehaviorTypeCode, dwellTime?: number, minObjectsInGroup?: number, clusterDistance?: number, shapes?: SearchShape[]) { 
        this.behaviorType = behaviorType;
        this.dwellTime = dwellTime;
        this.minObjectsInGroup = minObjectsInGroup;
        this.clusterDistance = clusterDistance;
        this.shapes = shapes;
    }

}


