import { StreamTypeCode } from '../enums/StreamTypeCode';
import { SensorResolutionCode } from '../enums/SensorResolutionCode';
import { FovGeoAttributes } from '../common/FovGeoAttributes';
import { StringKeyValue } from '../common/StringKeyValue';
import { Thresholds } from '../common/Thresholds';
import { SensorTypeCode } from '../enums/SensorTypeCode';
import { Coordinate } from '../common/Coordinate';
import { SensorDebugInfo } from '../common/SensorDebugInfo';
import { BaseEntity } from '../entities/BaseEntity';
import { SensorStatusCode } from '../enums/SensorStatusCode';

/* 
   Sensor type 
*/
export class Sensor extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Folder Id (empty for root folder) 
    public folderId: string;
 
    // Appliance Id 
    public applianceId: string;
 
    // Agent Id (Relevant for new pipeline only) 
    public agentId: string;
 
    // Sensor name 
    public name: string;
 
    // Sensor type: CCD | THERMAL 
    public type: SensorTypeCode;
 
    // Sensor status: UNDEFINED | ACTIVE | WARNING | ERROR | INACTIVE | SUSPENDED | PENDING 
    public status: SensorStatusCode;
 
    // Sensor real time state bit mask flags 
    public state: number;
 
    // When was the last state report update [Epoch milliseconds Timestamp] 
    public stateReportedOn: number;
 
    // Sensor video stream type: UNDEFINED | RTSP | ONVIF | CLIP | RTP | MULTICAST 
    public streamType: StreamTypeCode;
 
    // Sensor resolution (image quality): LOW (0) | MEDIUM (1) | HIGH (2) 
    public imageResolution: SensorResolutionCode;
 
    // Flag to indicate if sensor is recording video 
    public recording: boolean;
 
    // Flag to indicate if sensor should send metadata 
    public metadata: boolean;
 
    // Flag to indicate if anomaly detection is enabled for this sensor 
    public anomalyDetection: boolean;
 
    // Minimal time interval between alarms (in seconds) - 0 for no interval 
    public alarmInterval: number;
 
    // Max number of bounding box per frame 
    public maxBBoxPerFrame: number;
 
    // Max frame rate 
    public maxFrameRate: number;
 
    // Video stream source URI 
    public streamUri: string;
 
    // Sensor geo location (WGS-84 coordinate system) 
    public geoLocation: Coordinate;
 
    // Sensor FOV azimuth (0 - 359) 
    public azimuth: number;
 
    // Field of View Geo attributes (used for FOV to WGS-84 transformations) 
    public fovAttributes: FovGeoAttributes;
 
    // External id (camera id in external system) 
    public externalId: string;
 
    // User defined tags (list of key-value pairs) 
    public tags: StringKeyValue[];
 
    // Device path in the account hierarchy 
    public path: string;
 
    // Health notifications thresholds 
    public healthThresholds: Thresholds;
 
    // Sensor debug information flags 
    public debugInfo: SensorDebugInfo;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


