
/*  
*/
export class AuditLogServiceExportRequest {
 
    // Filter by user id (empty means no filter) 
    public userId: string;
 
    // Filter by time period (lower bound in epoch time milliseconds) - 0 for no filter 
    public from: number;
 
    // Filter by time period (upper bound in epoch time milliseconds) - 0 for no filter 
    public to: number;
 
    // Filter by item type or partial type - empty for no filter 
    public itemType: string;
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(userId?: string, from?: number, to?: number, itemType?: string, sort?: string, format?: string, fields?: string[]) { 
        this.userId = userId;
        this.from = from;
        this.to = to;
        this.itemType = itemType;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


