import { SensorStatusCode } from '../enums/SensorStatusCode';
import { StreamTypeCode } from '../enums/StreamTypeCode';
import { SensorTypeCode } from '../enums/SensorTypeCode';

/*  
*/
export class SensorsServiceExportRequest {
 
    // Filter by folder Id (empty for no filter) 
    public folderId: string;
 
    // If true, search all sensors in folder and all its sub-folders 
    public subFolders: boolean;
 
    // Filter by name or partial name or id 
    public search: string;
 
    // Filter by sensor type(s) (empty or UNDEFINED for no filter) 
    public type: SensorTypeCode[];
 
    // Filter by sensor status(es) (empty or UNDEFINED for no filter) 
    public status: SensorStatusCode[];
 
    // Filter by stream type(s) (empty or UNDEFINED for no filter) 
    public stream: StreamTypeCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Export results to a different format. Supported formats: csv | json | xml 
    public format: string;
 
    // List of fields to export (leave empty to export all fields) 
    public fields: string[];
 
    constructor(folderId?: string, subFolders?: boolean, search?: string, type?: SensorTypeCode[], status?: SensorStatusCode[], stream?: StreamTypeCode[], sort?: string, format?: string, fields?: string[]) { 
        this.folderId = folderId;
        this.subFolders = subFolders;
        this.search = search;
        this.type = type;
        this.status = status;
        this.stream = stream;
        this.sort = sort;
        this.format = format;
        this.fields = fields;
    }

}


