import { BaseEntity } from '../entities/BaseEntity';

/* 
   Search sessions tracking 
*/
export class SearchSession extends BaseEntity {
 
    // Search definition Id 
    public searchId: string;
 
    // Search progress (0..100) 
    public progress: string;
 
    // Is search completed 
    public completed: boolean;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


