
/*  
*/
export class SysApplianceGetLogsRequest {
 
    // Appliance Id 
    public id: string;
 
    // List of component Ids (empty value means get logs from device manager) 
    public componentId: string[];
 
    // Logs request start timestamp (Epoch millis) 
    public from: number;
 
    // Logs request end timestamp (Epoch millis) - 0 means current time 
    public to: number;
 
    constructor(id?: string, componentId?: string[], from?: number, to?: number) { 
        this.id = id;
        this.componentId = componentId;
        this.from = from;
        this.to = to;
    }

}


