
/* 
   Tree item for account hierarchy tree 
*/
export class TreeItem {
 
    // Item Id 
    public itemId: string;
 
    // Tree item type (0: account, 1: folder, 2: sensor) 
    public type: number;
 
    // Tree item name 
    public name: string;
 
    // Item type name (ACCOUNT | FOLDER | CCD | THERMAL) 
    public typeName: string;
 
    constructor(itemId?: string, type?: number, name?: string, typeName?: string) { 
        this.itemId = itemId;
        this.type = type;
        this.name = name;
        this.typeName = typeName;
    }

}


