
/* 
   Docker registry credentials to connect to docker
   Includes information where to access and get images 
*/
export class DockerCredentials {
 
    // Docker user to pull images (provided by the system) 
    public dockerUser: string;
 
    // Docker password to pull images (provided by the system) 
    public dockerPwd: string;
 
    constructor(dockerUser?: string, dockerPwd?: string) { 
        this.dockerUser = dockerUser;
        this.dockerPwd = dockerPwd;
    }

}


