import { BaseEntity } from '../entities/BaseEntity';

/* 
   Detection event type 
*/
export class EventStatistics extends BaseEntity {
 
    // Sample time (hour granularity) 
    public sampleTime: number;
 
    // Real time events count 
    public eventCount: number;
 
    // Anomaly events count 
    public anomalyCount: number;
 
    // Custom events count 
    public customCount: number;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


