
/* 
   ONVIF channel description
   This structure describes the list of channels (cameras, NVRs, DVRs etc) discovered in the network by ONVIF protocol 
*/
export class OnvifChannel {
 
    // Channel unique identifier 
    public uUID: string;
 
    // Channel name 
    public name: string;
 
    // Channel address 
    public address: string;
 
    // Attached to sensor Id 
    public sensorId: string;
 
    constructor(uUID?: string, name?: string, address?: string, sensorId?: string) { 
        this.uUID = uUID;
        this.name = name;
        this.address = address;
        this.sensorId = sensorId;
    }

}


