/* 
   Entity type code (represent entity type in the system) 
*/
export enum EntityTypeCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Account [1] 
    ACCOUNT = 1,
 
    // User [2] 
    USER = 2,
 
    // Appliance / Edge device [3] 
    APPLIANCE = 3,
 
    // Agent [4] 
    AGENT = 4,
 
    // Sensor [5] 
    SENSOR = 5,
 
    // Folder [6] 
    FOLDER = 6,
 
    // Rule [7] 
    RULE = 7,
 
    // Detection Event [8] 
    EVENT = 8,
 
    // Health Event [9] 
    HEALTH_EVENT = 9,
 
    // Sensor Status [10] 
    SENSOR_STATUS = 10,
 
    // Agent Status [11] 
    AGENT_STATUE = 11,
 
    // Appliance Status [12] 
    APPLIANCE_STATUS = 12,
 
    // Calendar [13] 
    CALENDAR = 13,
 
    // Schedule [14] 
    SCHEDULE = 14,
 
}