
/* 
   Service Account registration data model 
*/
export class ServiceAccountRegistration {
 
    // Service account name 
    public name: string;
 
    // Service account email (optional, for registration) 
    public email: string;
 
    // Service account description 
    public description: string;
 
    constructor(name?: string, email?: string, description?: string) { 
        this.name = name;
        this.email = email;
        this.description = description;
    }

}


