import { TimeUnitCode } from '../enums/TimeUnitCode';

/* 
   Anomaly sensitivity defines parameters to adjust thresholds for anomaly events 
*/
export class Sensitivity {
 
    // How many anomaly events in a time unit 
    public events: number;
 
    // Time unit (only DAY and WEEK are allowed) 
    public timeUnit: TimeUnitCode;
 
    constructor(events?: number, timeUnit?: TimeUnitCode) { 
        this.events = events;
        this.timeUnit = timeUnit;
    }

}


