import { ObjectInfo } from '../common/ObjectInfo';
import { BoundingBox } from '../common/BoundingBox';
import { BaseEntity } from '../entities/BaseEntity';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { EventStatusCode } from '../enums/EventStatusCode';
import { Coordinate } from '../common/Coordinate';
import { SeverityTypeCode } from '../enums/SeverityTypeCode';

/* 
   Search result item of search operation. The data structure is identical to an Event 
*/
export class SearchEvent extends BaseEntity {
 
    // Sensor Id 
    public sensorId: string;
 
    // External Sensor Id (for integrations) 
    public extSensorId: string;
 
    // Account Id 
    public accountId: string;
 
    // When the event was detected [Epoch milliseconds Timestamp] 
    public eventTime: number;
 
    // Rule Id 
    public ruleId: string;
 
    // External Rule Id (for integrations) 
    public extRuleId: string;
 
    // Event description (free text as provided by the event source - event engine, anomaly, custom, FR, LPR ...) 
    public description: string;
 
    // Detected behavior type (UNKNOWN | CROSSING | MOVING | GROUPING | OCCUPANCY ...) 
    public objectType: ObjectTypeCode;
 
    // Detected object type (UNKNOWN | PERSON | VEHICLE | BUS | TRACK ...) 
    public behaviorType: BehaviorTypeCode;
 
    // Validation status for event processing workflow: NEW | OPEN | CLOSE | IRRELEVANT | FALSE 
    public status: EventStatusCode;
 
    // Event geo location (WGS-84 coordinate system). This field is applicable only if the sensor is geo-referenced. 
    public geoLocation: Coordinate;
 
    // Event image path 
    public imagePath: string;
 
    // Event clip path 
    public clipPath: string;
 
    // Event severity level: UNDEFINED | LOW | MEDIUM | HIGH | CRITICAL 
    public severity: SeverityTypeCode;
 
    // Event image time [Epoch milliseconds Timestamp] 
    public eventImageTime: number;
 
    // Detected object information 
    public eventObjects: ObjectInfo[];
 
    // Detected object(s) bounding box(es) 
    public boundingBoxes: BoundingBox[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


