import { CommandStatusCode } from '../enums/CommandStatusCode';

/*  
*/
export class AppliancesServiceGetCommandsRequest {
 
    // Appliance Id 
    public id: string;
 
    // Filter by command status(es) (empty or UNDEFINED means no filter) 
    public status: CommandStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(id?: string, status?: CommandStatusCode[], sort?: string, page?: number, pageSize?: number) { 
        this.id = id;
        this.status = status;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


