import { Point } from '../common/Point';
import { Coordinate } from '../common/Coordinate';

/* 
   Control point maps FOV location (10000 x 10000 coordinate system) to WGS-84 coordinate system
   Control points are used for FOV geo-referencing 
*/
export class GeoControlPoint {
 
    // Point in the FOV (on the raster image) 
    public point: Point;
 
    // WGS-84 Coordinate (lan/lng) to match the control point 
    public coordinate: Coordinate;
 
    constructor(point?: Point, coordinate?: Coordinate) { 
        this.point = point;
        this.coordinate = coordinate;
    }

}


