import { TreeItem } from '../common/TreeItem';

/* 
   Tree node in account hierarchy tree 
*/
export class TreeNode {
 
    // Node Id 
    public id: string;
 
    // Node parent id (empty string to root node) 
    public parentId: string;
 
    // Node name 
    public name: string;
 
    // Node item 
    public item: TreeItem;
 
    // Children (sub nodes) 
    public children: TreeNode[];
 
    constructor(id?: string, parentId?: string, name?: string, item?: TreeItem, children?: TreeNode[]) { 
        this.id = id;
        this.parentId = parentId;
        this.name = name;
        this.item = item;
        this.children = children;
    }

}


