
/* 
   Sensor debug information 
*/
export class SensorDebugInfo {
 
    // Flag to indicate the agent to save Deep Learning image crops for debugging 
    public saveCrops: boolean;
 
    // Flag to indicate the agent to save frames for debugging 
    public saveFrames: boolean;
 
    // Flag to indicate the agent to save event images for debugging 
    public saveEventImages: boolean;
 
    constructor(saveCrops?: boolean, saveFrames?: boolean, saveEventImages?: boolean) { 
        this.saveCrops = saveCrops;
        this.saveFrames = saveFrames;
        this.saveEventImages = saveEventImages;
    }

}


