
/* 
   Account statistic summary (provide info about account sensors and devices number compared to previous month) 
*/
export class AccountStatSummary {
 
    // Account Id 
    public accountId: string;
 
    // Account name 
    public accountName: string;
 
    // Month id number (in the format of YYYYMM) 
    public monthId: number;
 
    // Total number of channels (sensors) 
    public channels: number;
 
    // Percentage of number of channels (sensors) compared to previous month 
    public channelsPercentage: number;
 
    // Total number of edge devices 
    public devices: number;
 
    // Percentage of number of edge devices compared to previous month 
    public devicesPercentage: number;
 
    constructor(accountId?: string, accountName?: string, monthId?: number, channels?: number, channelsPercentage?: number, devices?: number, devicesPercentage?: number) { 
        this.accountId = accountId;
        this.accountName = accountName;
        this.monthId = monthId;
        this.channels = channels;
        this.channelsPercentage = channelsPercentage;
        this.devices = devices;
        this.devicesPercentage = devicesPercentage;
    }

}


