import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { SensorStatusCode } from '../enums/SensorStatusCode';
import { StreamTypeCode } from '../enums/StreamTypeCode';
import { SensorTypeCode } from '../enums/SensorTypeCode';


/**
 * Services for sensor actions for system administrator only
 * @RequestHeader X-API-KEY The key to identify the application (portal)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysSensorsService {

  // URL to web api
  private baseUrl = '/sys/sensors';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Get single sensor by id
   * @Return: EntityResponse<Sensor>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find sensors by filters
   * @Return: QueryResponse<Sensor>
   */
  find(accountId?: string, folderId?: string, subFolders?: boolean, search?: string, type?: SensorTypeCode[], status?: SensorStatusCode[], stream?: StreamTypeCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (stream != null) { params.push(`stream=${stream}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Get single sensor health by sensor id
   * @Return: EntityResponse<SensorStatus>
   */
  getSensorHealthStatus(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}/health`);
  }

  /**
   * Get all sensors health status
   * @Return: EntitiesResponse<SensorStatus>
   */
  getSensorsHealthStatus() {
    return this.rest.get(`${this.baseUrl}/health`);
  }

  /**
   * Get sensor status history over time
   * @Return:  EntityResponse<SensorStatusTimeSeries>
   */
  getStatusOverTime(id?: string, from?: number, to?: number) {
    const params = new Array();		
    if (from != null) { params.push(`from=${from}`); }
    if (to != null) { params.push(`to=${to}`); }

		return this.rest.get(`${this.baseUrl}/${id}/status/overtime`, ...params);
  }

  /**
   * Aggregate sensors count distribution by type
   * @Return:  EntityResponse<DistributionOfLong>
   */
  getSensorsCountByType(accountId?: string, folderId?: string, subFolders?: boolean, sensorType?: SensorTypeCode, status?: SensorStatusCode, streamType?: StreamTypeCode) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorType != null) { params.push(`sensorType=${sensorType}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (streamType != null) { params.push(`streamType=${streamType}`); }

		return this.rest.get(`${this.baseUrl}/count/by-type`, ...params);
  }

  /**
   * Aggregate sensors count distribution by stream type
   * @Return:  EntityResponse<DistributionOfLong>
   */
  getSensorsCountByStream(accountId?: string, folderId?: string, subFolders?: boolean, sensorType?: SensorTypeCode, status?: SensorStatusCode, streamType?: StreamTypeCode) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorType != null) { params.push(`sensorType=${sensorType}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (streamType != null) { params.push(`streamType=${streamType}`); }

		return this.rest.get(`${this.baseUrl}/count/by-stream`, ...params);
  }

  /**
   * Aggregate sensors count distribution by status
   * @Return:  EntityResponse<DistributionOfLong>
   */
  getSensorsCountByStatus(accountId?: string, folderId?: string, subFolders?: boolean, sensorType?: SensorTypeCode, status?: SensorStatusCode, streamType?: StreamTypeCode) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (folderId != null) { params.push(`folderId=${folderId}`); }
    if (subFolders != null) { params.push(`subFolders=${subFolders}`); }
    if (sensorType != null) { params.push(`sensorType=${sensorType}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (streamType != null) { params.push(`streamType=${streamType}`); }

		return this.rest.get(`${this.baseUrl}/count/by-status`, ...params);
  }

}
