
/* 
   Rule default encapsulates boundaries and default values of rule parameter 
*/
export class RuleDefault {
 
    // Min value (Lower bound) 
    public min: number;
 
    // Max value (Upper bound) 
    public max: number;
 
    // Default value 
    public defaultValue: number;
 
    constructor(min?: number, max?: number, defaultValue?: number) { 
        this.min = min;
        this.max = max;
        this.defaultValue = defaultValue;
    }

}


