import { ColorCode } from '../enums/ColorCode';
import { ColorTypeCode } from '../enums/ColorTypeCode';

/* 
   Color definitions to search for 
*/
export class SearchColor {
 
    // Minimal confidence, the user should select between "high" and "normal" sensitivity 
    public conf: number;
 
    // Object color to search for: WHITE | BLACK ... 
    public color: ColorCode;
 
    // Object color type to search for: UNDEFINED | UPPER_BODY | LOWER_BODY ... 
    public type: ColorTypeCode;
 
    constructor(conf?: number, color?: ColorCode, type?: ColorTypeCode) { 
        this.conf = conf;
        this.color = color;
        this.type = type;
    }

}


