import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { RuleDefault } from '../common/RuleDefault';
import { ObjectTypeNode } from '../common/ObjectTypeNode';

/* 
   Rule specification describe rule parameters 
*/
export class RuleSpec {
 
    // Behavior types for this rule 
    public behaviorType: BehaviorTypeCode;
 
    // Rule type name 
    public ruleTypeName: string;
 
    // Available object types for this rule 
    public objectTypes: ObjectTypeCode[];
 
    // Is this rule requires line drawing (true) or area drawing (false) 
    public isLineDrawing: boolean;
 
    // Dwell Time 
    public dwellTime: RuleDefault;
 
    // Number of people In Group (for grouping and occupancy rules) 
    public peopleInGroup: RuleDefault;
 
    // Max distance between people to be considered as group (for Grouping rule) 
    public clusterDistance: RuleDefault;
 
    // Time interval [seconds] between statistics reports (for statistics rules) 
    public intervalTime: RuleDefault;
 
    // Is reference crop required (for Asset Protection) 
    public referenceCrop: boolean;
 
    // Available object types for this rule organized in hierarchy 
    public objectHierarchy: ObjectTypeNode[];
 
    constructor(behaviorType?: BehaviorTypeCode, ruleTypeName?: string, objectTypes?: ObjectTypeCode[], isLineDrawing?: boolean, dwellTime?: RuleDefault, peopleInGroup?: RuleDefault, clusterDistance?: RuleDefault, intervalTime?: RuleDefault, referenceCrop?: boolean, objectHierarchy?: ObjectTypeNode[]) { 
        this.behaviorType = behaviorType;
        this.ruleTypeName = ruleTypeName;
        this.objectTypes = objectTypes;
        this.isLineDrawing = isLineDrawing;
        this.dwellTime = dwellTime;
        this.peopleInGroup = peopleInGroup;
        this.clusterDistance = clusterDistance;
        this.intervalTime = intervalTime;
        this.referenceCrop = referenceCrop;
        this.objectHierarchy = objectHierarchy;
    }

}


