import { LongTuple } from '../common/LongTuple';

/* 
   Distribution series result - keys are long values 
*/
export class DistributionOfLong {
 
    // Label of series 
    public label: string;
 
    // Array of key-count values (both of type int64) 
    public data: LongTuple[];
 
    constructor(label?: string, data?: LongTuple[]) { 
        this.label = label;
        this.data = data;
    }

}


