import { ProductTypeCode } from '../enums/ProductTypeCode';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   SW Configuration type 
*/
export class Configuration extends BaseEntity {
 
    // Configuration description 
    public description: string;
 
    // Current configuration version 
    public currentVersion: string;
 
    // List of target product types this configuration applies to 
    public targets: ProductTypeCode[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


