import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { BehaviorTypeCode } from '../enums/BehaviorTypeCode';

/* 
   @WebSocketMessage
   Payload for events filter socket message 
*/
export class SocketEventsFilterPayload {
 
    // List of Sensor Ids 
    public sensorIds: string[];
 
    // List of External Sensor Ids 
    public extSensorIds: string[];
 
    // List of Rule Ids 
    public ruleIds: string[];
 
    // List of External Rule Ids 
    public extRuleIds: string[];
 
    // List of object types 
    public objectTypes: ObjectTypeCode[];
 
    // List of behavior (rule) types 
    public behaviorTypes: BehaviorTypeCode[];
 
    constructor(sensorIds?: string[], extSensorIds?: string[], ruleIds?: string[], extRuleIds?: string[], objectTypes?: ObjectTypeCode[], behaviorTypes?: BehaviorTypeCode[]) { 
        this.sensorIds = sensorIds;
        this.extSensorIds = extSensorIds;
        this.ruleIds = ruleIds;
        this.extRuleIds = extRuleIds;
        this.objectTypes = objectTypes;
        this.behaviorTypes = behaviorTypes;
    }

}


