import { AnalysisResult } from '../common/AnalysisResult';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Sensor training analysis results
   This data is used by the training process of Anomaly Detection
   The entity Id is the sensorId 
*/
export class SensorAnalysisResults extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // List of analysis results (model + file path) 
    public results: AnalysisResult[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


