import { ApplianceStatusCode } from '../enums/ApplianceStatusCode';

/* 
   Data point in a series representing appliance status values in a specific timestamp 
*/
export class ApplianceStatusTimestamped {
 
    // Timestamp (Epoch milliseconds) 
    public timestamp: number;
 
    // Data point label 
    public label: string;
 
    // Appliance status 
    public value: ApplianceStatusCode;
 
    constructor(timestamp?: number, label?: string, value?: ApplianceStatusCode) { 
        this.timestamp = timestamp;
        this.label = label;
        this.value = value;
    }

}


