import { SearchObject } from '../entities/SearchObject';
import { BaseEntity } from '../entities/BaseEntity';
import { SearchScopeCode } from '../enums/SearchScopeCode';
import { SearchTimeCode } from '../enums/SearchTimeCode';
import { TimeFrame } from '../common/TimeFrame';
import { SearchBehavior } from '../entities/SearchBehavior';

/* 
   Query definitions for ad-hoc search specification 
*/
export class SearchDefinition extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // User Id who created the search 
    public userId: string;
 
    // Search name 
    public name: string;
 
    // Search Tag (for organizing search definitions in folder-like structure) 
    public tag: string;
 
    // Account scope key (where to start the search in the hierarchy) 
    public scopeKey: string;
 
    // Is the search definition is private (accessible by owner only) or public (accessible by all account users) 
    public privateSearch: boolean;
 
    // Scope of source: UNDEFINED | SENSORS | FOLDERS | GEO_CIRCLE | GEO_POLYGON 
    public sourceScope: SearchScopeCode;
 
    // Time scope to search for (CUSTOM or Duration format [PnDTnHnMn] e.g. PT24H) 
    public timeScope: SearchTimeCode;
 
    // Time frame to search for (applicative only if search scope is CUSTOM) 
    public timeFrame: TimeFrame;
 
    // List of sensor Ids included in the query (or "GEO") 
    public sensorIds: string[];
 
    // Search behavior attributes 
    public behavior: SearchBehavior;
 
    // Objects attributes to search for (what) 
    public objects: SearchObject[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


