import { ProductTypeCode } from '../enums/ProductTypeCode';

/*  
*/
export class SysConfigurationsServiceFindRequest {
 
    // Filter by product target type(s) (Use UNDEFINED for all products) 
    public target: ProductTypeCode[];
 
    // Search by name or description 
    public search: string;
 
    // Filter by status (empty or UNDEFINED means no filter) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(target?: ProductTypeCode[], search?: string, sort?: string, page?: number, pageSize?: number) { 
        this.target = target;
        this.search = search;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


