import { ColorTypeCode } from '../enums/ColorTypeCode';
import { ColorCode } from '../enums/ColorCode';

/* 
   Metadata object color description 
*/
export class ObjectColor {
 
    // Color type 
    public colorType: ColorTypeCode;
 
    // Primary color 
    public primary: ColorCode;
 
    // The confidence in the primary color of this object (in percent) 
    public primaryConf: number;
 
    // Secondary color 
    public secondaryColor: ColorCode;
 
    // The confidence in the secondary color of this object (in percent) 
    public secondaryConf: number;
 
    constructor(colorType?: ColorTypeCode, primary?: ColorCode, primaryConf?: number, secondaryColor?: ColorCode, secondaryConf?: number) { 
        this.colorType = colorType;
        this.primary = primary;
        this.primaryConf = primaryConf;
        this.secondaryColor = secondaryColor;
        this.secondaryConf = secondaryConf;
    }

}


