import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { Coordinate } from '../common/Coordinate';
import { AccountTypeCode } from '../enums/AccountTypeCode';
import { AccountStatusCode } from '../enums/AccountStatusCode';


/**
 * List of account related actions 
 */
@Injectable()
export class AccountsService {

  // URL to web api
  private baseUrl = '/accounts';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Find list of accounts and filter
   * @Return: QueryResponse<Account>
   */
  find(search?: string, type?: AccountTypeCode[], status?: AccountStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Export list of accounts and filter
   * @Return: StreamContent
   */
  exportFormat(name?: string, type?: AccountTypeCode[], status?: AccountStatusCode[], sort?: string, format?: string, fields?: string[]) {
    const params = new Array();		
    if (name != null) { params.push(`name=${name}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (format != null) { params.push(`format=${format}`); }
    if (fields != null) { params.push(`fields=${fields}`); }

		return this.rest.download(`accounts`,`${this.baseUrl}/export`, ...params);
  }

  /**
   * Get single account by id
   * @Return: EntityResponse<Account>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Change account geo-area (polygon)
   * @Return: EntityResponse<Account>
   */
  changeGeoArea(body?: Coordinate[]) {
    return this.rest.put(`${this.baseUrl}/geoArea`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change account geo-location (center)
   * @Return: EntityResponse<Account>
   */
  changeGeoLocation(body?: Coordinate) {
    return this.rest.put(`${this.baseUrl}/geoLocation`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change account timezone
   * @Return: EntityResponse<Account>
   */
  changeTimezone(body?: string) {
    return this.rest.put(`${this.baseUrl}/timezone`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Get default geo location
   * @Return: EntityResponse<Coordinate> The account location
   */
  getDefaultLocation() {
    return this.rest.get(`${this.baseUrl}/geoLocation`);
  }

  /**
   * Get aggregated summary report of total number of sensors and devices with error / warning status
   * @Return: EntitiesResponse<DistributionOfLong> The list of health status distribution
   */
  getAccountHealthSummary() {
    return this.rest.get(`${this.baseUrl}/health`);
  }

}
