import { SocketHealthStatusNotificationPayload } from '../sockets/SocketHealthStatusNotificationPayload';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any new event in the registered account 
*/
export class SocketEventNotification {
 
    // Message OpCode (1041) 
    public op: number;
 
    // Message payload 
    public payload: SocketHealthStatusNotificationPayload;
 
    constructor(op?: number, payload?: SocketHealthStatusNotificationPayload) { 
        this.op = op;
        this.payload = payload;
    }

}


