import { StringKeyValue } from '../common/StringKeyValue';
import { RuntimeStatusCode } from '../enums/RuntimeStatusCode';

/* 
   SW Component version type 
*/
export class ComponentConfiguration {
 
    // Component Id 
    public id: string;
 
    // Component group name 
    public component: string;
 
    // Docker image name (including tag and docker registry) 
    public dockerImage: string;
 
    // List of environment variables to inject (as key-value pairs) 
    public variables: StringKeyValue[];
 
    // List of mount points to inject (as key-value pairs) 
    public mounts: StringKeyValue[];
 
    // Runtime component status 
    public status: RuntimeStatusCode;
 
    constructor(id?: string, component?: string, dockerImage?: string, variables?: StringKeyValue[], mounts?: StringKeyValue[], status?: RuntimeStatusCode) { 
        this.id = id;
        this.component = component;
        this.dockerImage = dockerImage;
        this.variables = variables;
        this.mounts = mounts;
        this.status = status;
    }

}


