import { RecurrentTimeFrame } from '../common/RecurrentTimeFrame';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Schedule is a list of recurrent time frames to specify active analytics
   Schedule is associated with account and optionally with folder and multiple schedules can be specified.
   When defining a rule, it can be scheduled by one of the schedules associated with the sensor's folder, it's parent folder, it's parent's parent folder and so on up to the account 
*/
export class Schedule extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Folder Id (use empty string to associate the schedule to the account level) 
    public folderId: string;
 
    // Schedule name 
    public name: string;
 
    // Schedule description 
    public description: string;
 
    // Is this schedule is enabled 
    public enabled: boolean;
 
    // Account timezone (IANA format, default is UTC) 
    public timezone: string;
 
    // List of time frames 
    public timeFrames: RecurrentTimeFrame[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


