import { BoundingBox } from '../common/BoundingBox';
import { Point } from '../common/Point';
import { Coordinate } from '../common/Coordinate';
import { ObjectTypeCode } from '../enums/ObjectTypeCode';
import { ObjectColor } from '../common/ObjectColor';
import { Dimension } from '../common/Dimension';

/* 
   Metadata object instance info
   One instance of the object in the scene 
*/
export class ObjectInstance {
 
    // Instance timestamp [Epoch milliseconds Timestamp] 
    public timestamp: number;
 
    // Object type (class) 
    public objectType: ObjectTypeCode;
 
    // Level of classification confidence (0 - 1000) 
    public confidence: number;
 
    // A boolean determining if this point is currently stopped or moving 
    public isStopped: boolean;
 
    // An array defining object colors. Each color is defined by a type with a primary and secondary color defined 
    public colors: ObjectColor[];
 
    // Direction between -3141 and 3141 (-pi to pi in integers), where 0 is up 
    public direction: number;
 
    // Instance dimension (height and width in mm) 
    public dimension: Dimension;
 
    // Instance bounding box in the frame 
    public boundingBox: BoundingBox;
 
    // Instance speed in meters/sec * 1000 (millimeter/sec) 
    public speed: number;
 
    // Instance approximate size in 1000*(m^2) 
    public size: number;
 
    // Instance location in the image 
    public position: Point;
 
    // Instance location in the world (WGS-84 coordinate system) 
    public location: Coordinate;
 
    constructor(timestamp?: number, objectType?: ObjectTypeCode, confidence?: number, isStopped?: boolean, colors?: ObjectColor[], direction?: number, dimension?: Dimension, boundingBox?: BoundingBox, speed?: number, size?: number, position?: Point, location?: Coordinate) { 
        this.timestamp = timestamp;
        this.objectType = objectType;
        this.confidence = confidence;
        this.isStopped = isStopped;
        this.colors = colors;
        this.direction = direction;
        this.dimension = dimension;
        this.boundingBox = boundingBox;
        this.speed = speed;
        this.size = size;
        this.position = position;
        this.location = location;
    }

}


