import { ApplianceStatusCode } from '../enums/ApplianceStatusCode';

/*  
*/
export class SysAppliancesServiceFindRequest {
 
    // Filter by account Id (empty for no filter) 
    public accountId: string;
 
    // Filter by name or mac address or serial number 
    public search: string;
 
    // Filter by appliance status(es) (empty or UNDEFINED means no filter) 
    public status: ApplianceStatusCode[];
 
    // Sort by field and direction: (e.g. time+ = sort by time asc, id- = sort by id desc) 
    public sort: string;
 
    // Page number (for pagination) 
    public page: number;
 
    // Number of items per page (for pagination) 
    public pageSize: number;
 
    constructor(accountId?: string, search?: string, status?: ApplianceStatusCode[], sort?: string, page?: number, pageSize?: number) { 
        this.accountId = accountId;
        this.search = search;
        this.status = status;
        this.sort = sort;
        this.page = page;
        this.pageSize = pageSize;
    }

}


