import { BaseEntity } from '../entities/BaseEntity';

/* 
   License information includes on-premises deployment's license restrictions 
*/
export class License extends BaseEntity {
 
    // Customer Id (Provided by the CRM) 
    public customerId: string;
 
    // Customer name (Provided by the CRM) 
    public customerName: string;
 
    // Deployment name (site or system name) 
    public deploymentName: string;
 
    // When the license will expire [Epoch milliseconds Timestamp] - 0 for no expiration 
    public expiration: number;
 
    // Limit number of connected channels 
    public channelsLimit: number;
 
    // Limit number of virtual devices 
    public devicesLimit: number;
 
    // List of enabled features 
    public features: string[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


