import { ComponentVariables } from '../entities/ComponentVariables';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Appliance Profile entity describes custom device profile to override package configuration variables for specific use cases 
*/
export class ApplianceProfile extends BaseEntity {
 
    // Profile name 
    public name: string;
 
    // Account Id 
    public accountId: string;
 
    // Is the profile enabled? (the user can temporary disable profile so no parameters will be override) 
    public enabled: boolean;
 
    // List of Configuration overrides 
    public overrides: ComponentVariables[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


