/* 
   Map client code (represent which map client library to use in the UI) 
*/
export enum MapClientCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Google maps client [1] 
    GOOGLE = 1,
 
    // LeafLet map client [2] 
    LEAFLET = 2,
 
    // ESRI client (not implemented) [3] 
    ESRI = 3,
 
    // MapInfo client (not implemented) [4] 
    MAPINFO = 4,
 
}