import { BaseEntity } from '../entities/BaseEntity';
import { Coordinate } from '../common/Coordinate';
import { HealthThresholds } from '../common/HealthThresholds';

/* 
   Folder (container for sensors and sub folders) 
*/
export class Folder extends BaseEntity {
 
    // Folder parent Id (Empty if folder is in the root) 
    public parentId: string;
 
    // Account Id 
    public accountId: string;
 
    // Folder name 
    public name: string;
 
    // Folder external id (how the folder is identified in other systems) 
    public externalId: string;
 
    // Folder hierarchy depth 
    public depth: number;
 
    // Folder path in the account hierarchy 
    public path: string;
 
    // Folder geo area polygon (the bounding polygon of the area) 
    public geoArea: Coordinate[];
 
    // Folder geo location (the center coordinate of the account region) 
    public geoLocation: Coordinate;
 
    // Folder timezone (IANA format, default is UTC) 
    public timezone: string;
 
    // Health notifications thresholds for sensors and appliances 
    public healthThresholds: HealthThresholds;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


