import { OnvifChannel } from '../entities/OnvifChannel';

/* 
   Edge appliance ONVIF discovery full report <br>
   This structure describes the list of channels (cameras, NVRs, DVRs etc) discovered in the network by ONVIF protocol 
*/
export class ApplianceDiscovery {
 
    // List of ONVIF channels descriptors 
    public channels: OnvifChannel[];
 
    constructor(channels?: OnvifChannel[]) { 
        this.channels = channels;
    }

}


