import { Coordinate } from '../common/Coordinate';

/* 
   Geo spatial circle 
*/
export class GeoCircle {
 
    // Circle center coordinate 
    public center: Coordinate;
 
    // Radius (in meters) 
    public radius: number;
 
    constructor(center?: Coordinate, radius?: number) { 
        this.center = center;
        this.radius = radius;
    }

}


