import { BaseEntity } from '../entities/BaseEntity';

/* 
   Query definitions for report specification 
*/
export class ReportDefinition extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // User who created the report 
    public userId: string;
 
    // Report name 
    public name: string;
 
    // Search Tag (for organizing report definitions in folder-like structure) 
    public tag: string;
 
    // Is the search definition is private (accessible by owner only) or public (accessible by all account users) 
    public privateReport: boolean;
 
    // Report URL in case of external report (e.g. Kibana) 
    public reportUrl: string;
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


