import { StringKeyValue } from '../common/StringKeyValue';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   SW Configuration template type 
*/
export class ConfigurationTemplate extends BaseEntity {
 
    // Template name 
    public name: string;
 
    // Docker image name (including tag and docker registry) 
    public dockerImage: string;
 
    // List of environment variables with default values 
    public variables: StringKeyValue[];
 
    // List of mount points to inject (as key-value pairs) 
    public mounts: StringKeyValue[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


