import { AccountTypeCode } from '../enums/AccountTypeCode';

/* 
   Account types aggregator summary 
*/
export class AccountTypeSummary {
 
    // User role in the account 
    public type: AccountTypeCode;
 
    // Account type name 
    public typeName: string;
 
    // Total number of accounts of this type 
    public accounts: number;
 
    // Percentage of accounts of this type 
    public accountsPercentage: number;
 
    // Total number of channels (sensors) for all accounts of this type 
    public channels: number;
 
    // Percentage of number of channels (sensors) for all accounts of this type 
    public channelsPercentage: number;
 
    // Total number of edge devices 
    public devices: number;
 
    // Percentage of number of edge devices 
    public devicesPercentage: number;
 
    constructor(type?: AccountTypeCode, typeName?: string, accounts?: number, accountsPercentage?: number, channels?: number, channelsPercentage?: number, devices?: number, devicesPercentage?: number) { 
        this.type = type;
        this.typeName = typeName;
        this.accounts = accounts;
        this.accountsPercentage = accountsPercentage;
        this.channels = channels;
        this.channelsPercentage = channelsPercentage;
        this.devices = devices;
        this.devicesPercentage = devicesPercentage;
    }

}


